.app {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #edebeb;
}

.gameboard {
    width: 300px;
    height: 300px;
    display: flex;
    flex-wrap: wrap;
    border: solid 1px grey;
}

.square {
    height: 100px;
    width: 100px;
    border: solid 2px grey;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
}

.square:hover {
    background-color: lightgray;
}

.circle {
    height: 70px;
    width: 70px;
    border-radius: 50%;
    border: 10px solid cornflowerblue;
    box-sizing: border-box;
}

.cross {
    height: 70px;
    width: 70px;
    position: relative;
    transform: rotate(45deg);
}

.cross:before, .cross:after {
    content: "";
    position: absolute;
    background-color: indianred;;
}

.cross:before {
    left: 50%;
    width: 16%;
    margin-left: -8%;
    height: 100%;
}

.cross:after {
    top: 50%;
    height: 16%;
    margin-top: -8%;
    width: 100%;
}

.message {
    font-size: 30px;
    color: darkslategrey;
}
